<template>
	<view class="box" v-if="order.orderStatus">
		<!-- 配送订单显示地址 -->
		<view class="adress-box" v-if="order.orderHeadShopList[0].receiveMode=='PEI_SONG'">
			<view class="name-box font-bold">
				<text class="name">
					{{order.orderReceiver.receiverName}}
				</text>
				<text class="phone">
					{{order.orderReceiver.receiverMobileNo}}
				</text>
			</view>
			<view class="address">
				<view class="text">
					{{order.orderReceiver.address1}}{{order.orderReceiver.address2}}
				</view>
			</view>
		</view>
		<view class="dott_line" v-if="order.orderHeadShopList[0].receiveMode=='PEI_SONG'"></view>
		<!-- 未支付 -->
		<view class="order-tips" v-if="order.orderStatus=='NOPAY'">
			<view class="text font-bold">您已下单，请及时完成支付</view>
			<view class="text time">订单在{{formatCut}}之内尚未支付，订单将会自动关闭</view>
		</view>
		<!-- 待发货 -->
		<view class="order-tips" v-if="order.orderStatus=='DEAL'">
			<!-- 寄售 -->
			<view class="text font-bold" v-if="order.orderHeadShopList[0].receiveMode=='JI_SHOU'">商品将会寄售给平台</view>
			<!-- 配送 -->
			<view class="text font-bold" v-else>商家会尽快为您安排发货</view>
		</view>
		<!-- 已完成 -->
		<view class="order-tips" v-if="order.orderStatus=='FINISH'">
			<view class="text font-bold">订单已交易成功</view>
		</view>
		<!-- 取消订单 -->
		<view class="order-tips" v-if="order.orderStatus=='CANCEL'">
			<view class="text font-bold">您已取消订单</view>
		</view>
		<template v-for="(item,k) in order.orderHeadShopList">
			<view class="main-box" :key="k">
				<view class="shop-title">
					<view :url="'/pages/goods/shop?merchantShopId='+item.merchantShopId" class="shop-name">
						<view class="iconfont icondianpu1"></view>
						<view class="name">{{item.merchantShopName}}</view>
					</view>
					<view class="shop-order" v-if="order.orderHeadShopList[0].receiveMode=='JI_SHOU'">
						寄售
					</view>
					<view class="shop-order" v-else>
						{{orderShop_status[item.orderShopStatus]}}
					</view>
				</view>
				<template v-for="(val,i) in item.orderDetList">
					<navigator :url="'/pages/goods/detail?seqId='+val.spuSeqId+'&merchantShopId='+val.merchantShopId" class="goods-box"
					 :key="i">
						<image lazy-load class="img scale11" :src="val.skuUrl" mode="aspectFill"></image>
						<view class="right">
							<view class="top">
								<view class="title">
									{{val.skuName}}
								</view>
								<view class="types-box">
									<view class="type">{{val.specNames}} </view>
									<view class="num">X {{val.sl}} </view>
								</view>
							</view>
							<view class="price bold">
								 <text class="icon" v-if="!$common.isAorC(order.orderType)">￥</text>
								{{val.orginPrice | formatNum}}
								<text class="font-small" v-if="$common.isAorC(order.orderType)">{{money_text[order.orderType]}}</text>
							</view>
						</view>
					</navigator>
				</template>
				<view class="line-box">
					<view class="line">
						<view class="title">
							配送方式
							<text class="send">{{receive_map[item.receiveMode]}}</text>
						</view>
						<view class="price" v-if="item.logisticPayType=='CUSTOMER'">
							￥{{item.logisticsFee | formatNum}}
						</view>
						<view class="price" v-else>
							免运费
						</view>
					</view>
					<view class="line" style="display: none;">
						<view class="title">
							优惠活动
						</view>
						<view class="price">
							满400减50
						</view>
					</view>
					<view class="line all-line">
						<view class="title">
							订单备注
						</view>
						<view class="remark" disbaled="disabled">{{item.remarks==null?'':item.remarks}}</view>
						</view>
					<view class="line">
						<view class="title">
						</view>
						<view class="price">
							<text class="num">共{{item.orderDetList.length}}件</text>
							<text>小计：</text>
							
							<text class="total-price font-bold">
								<text v-if="!$common.isAorC(order.orderType)">￥</text>
								<!-- 团批劵 -->
								<template v-if="order.orderType=='COUPON'">
									{{item.couponMoney | formatNum}}
								</template>
								<!--活动积分 -->
								<template v-else-if="order.orderType=='ACTIVE_POINT'">
									{{item.jfMoney | formatNum}}
								</template>
								<!--其他 -->
								<template v-else>
									{{item.totalMoney | formatNum}}
								</template>
								<text class="font-small" v-if="$common.isAorC(order.orderType)">{{money_text[order.orderType]}}</text>
							</text>
						</view>
					</view>
				</view>
				<view class="kefu">
					<button open-type="contact" class="border-btn border-gray fix-btn no-btn">联系客服</button>
					<!-- 物流已发出 -->
					<navigator v-if="item.orderShopStatus=='SEND'||item.orderShopStatus=='SIGN'" :url="'/pages/member/logistics_info?com='+item.logisticsCompany+'&num='+item.logisticsOpenId+'&phone='+order.orderReceiver.receiverMobileNo" class="bg-btn" style="margin-left: 40rpx;">查看物流</navigator>
					<!-- 物流已发出 订单未签收 -->
					<view v-if="item.orderShopStatus=='SEND'" @tap="confirmOrder(item.seqId,k)" class="bg-btn" style="margin-left: 40rpx;">确认收货</view>
				</view>
			</view>
		</template>
		
		<view class="main-box">
			<view class="order-info-title">
				总计：
				<text class="price font-bold">
					<text v-if="!$common.isAorC(order.orderType)">￥</text>
					<!-- 团批劵 -->
					<template v-if="order.orderType=='COUPON'">
						{{order.couponMoney | formatNum}}
					</template>
					<!--活动积分 -->
					<template v-else-if="order.orderType=='ACTIVE_POINT'">
						{{order.jfMoney | formatNum}}
					</template>
					<!--其他 -->
					<template v-else>
						{{order.totalMoney | formatNum}}
					</template>
					<text class="font-small" v-if="$common.isAorC(order.orderType)">{{money_text[order.orderType]}}</text>
				</text>
				
				
			</view>
			<view class="line-box order-line">
				<view class="line">
					<view class="title">
						订单编号
					</view>
					<view class="price" @tap="copy(order.sheetId)">
						{{order.sheetId}}
						<text class="iconfont iconfuzhi-"></text>
					</view>
				</view>
				<view class="line">
					<view class="title">
						下单时间
					</view>
					<view class="price">
						{{order.orderDate}}
					</view>
				</view>
				
			</view>
			
		</view>
		<view class="main-box" v-if="order.orderStatus=='NOPAY'">
			<view class="pay-title">
				支付方式
			</view>
			<view class="pay-list">
				<view class="iconfont iconweixinzhifu "></view>
				<view class="iconfont iconlujing iconxuanzhong"></view>
			</view>
			
		</view>
		<!-- 未支付 -->
		<template v-if="order.orderStatus=='NOPAY'">
			<view class="blank"></view>
			<view class="fix-box">
				<view class="border-btn" @tap="cancelOrder(order.seqId)">
					取消订单
				</view>
				<view class="bg-btn active" @tap="weixinPay(order.seqId)">
					立即付款
				</view>
			</view>
		</template>
		<template v-if="order.orderStatus=='FINISH'">
		</template>
		<!-- <template v-if="order.orderStatus=='DEAL'&&order.payMode=='ONLINE'">
			<view class="blank"></view>
			<view class="fix-box">
				<view class="bg-btn active">
					立即使用
				</view>
			</view>
		</template> -->
		
	</view>
</template>

<script>
	let sheetId=""
	let cut1=null;
	let duration=12*60*60;  //自动取消时间
	export default {
		data() {
			return {
				 // ZI_TI 自提,PEI_SONG 配送，OFFLINE_SCAN 线下扫码,JI_SHOU 寄售
				order:{
					orderHeadShopList:[{
						orderDetList:[]
					}],
					orderReceiver:{}
				},
				receive_map:{
					'ZI_TI':'自提',
					'PEI_SONG':'物流配送',
					'OFFLINE_SCAN':'线下扫码',
					'JI_SHOU':'寄售',
				},
				order_status: {
					'NOPAY': '待付款',
					'DEAL': '待发货',
					'FINISH': '已完成',
					'FAILED': '处理失败',
					'CANCEL': '取消订单'
				},
				orderShop_status:{
					'INIT': '',
					'DEAL': '待发货',
					'SEND': '已发货',
					'SIGN': '已签收'
				},
				money_text:{
					'ACTIVE_POINT':'积分',
					'COUPON':'团批劵',
					},
				cut_time:0
			}
		},
		
		onLoad(option) {
			sheetId=option.sheetId;
			this.loadDetail()
		},
		methods: {
			// 微信支付
			weixinPay(seqId) {
				// #ifdef MP-WEIXIN
				let that = this;
				that.$api.post('/payInfo/submitOrderPay', {
					"orderHeadSeqId": seqId,
					"type": 1
				}).then(res => {
					if (res.code == 200) {
						let paymentData = res.data;
						uni.requestPayment({
							timeStamp: paymentData.timeStamp,
							nonceStr: paymentData.nonceStr,
							package: paymentData.package,
							signType: 'MD5',
							paySign: paymentData.paySign,
							success: (res) => {
								uni.showToast({
									title: "支付成功"
								})
								this.order.orderStatus='DEAL'
							},
							fail: (res) => {
								uni.showModal({
									content: "支付失败",
									showCancel: false
								})
							},
							complete: (res) => {
								console.log(res);
							}
						})
					} else if (res.code == 10012) {
						uni.navigateTo({
							url: '../login/login_wechat'
						})
					} 
				})
				// #endif
			},
			// 取消订单
			cancelOrder(seqId) {
				let self=this;
				uni.showModal({
				    title: '提示',
				   content:'确认取消订单？',
				    success: function (res) {
				        if (res.confirm) {
				           self.$api.get('/order/updateCancelStatus',{params:{
				           		seqId:seqId,
								orderStatus:'CANCEL'
				           }}).then(res=>{
								if(res.code==200){
									self.$box.toast(res.msg)
									self.order.orderStatus='CANCEL'
								}
				           })
				        } else if (res.cancel) {
				        }
				    }
				});
			},
			// 确认收货
			confirmOrder(seqId,k){
				this.$api.get('/order/updateConfirmStatus',{params:{
					seqId:seqId,
					orderShopStatus:'SIGN'
				}}).then(res=>{
					if(res.code==200){
					this.$box.toast(res.msg)
					this.order.orderHeadShopList[k].orderShopStatus='SIGN'
					}
				})
			},
			// 倒计时
			cutTime() {
				let self = this;
				self.cut_time = self.cut_time - Math.round(new Date().getTime() / 1000);
				if (this.cut_time > 0) {
					if (cut1) {
						clearInterval(cut1)
						cut1 = null;
					}
					cut1 = setInterval(() => {
						self.cut_time = self.cut_time - 1;
						if (self.cut_time <= 0) {
							clearInterval(cut1);
						}
					}, 1000)
				}
			},
			loadDetail(){
				this.$api.get('/order/getSheet',{params:{
					sheetId:sheetId
				}}).then(res=>{
					if(res.code==200) {
						let data=res.data;
						this.order=res.data;
						// 未支付显示倒计时
						if(data.orderStatus=='NOPAY'){
							let data_create=data.createDate.replace(/-/g, '/');//兼容
							let create_time=Math.round(new Date(data_create).getTime() / 1000);
							let end_time=create_time+duration;
							let now_time=Math.round(new Date().getTime() / 1000);
							let cut_time=end_time-now_time;
							if(now_time<end_time){
								this.cut_time=cut_time+ Math.round(new Date().getTime() / 1000);
								this.cutTime()
							}
						}
						
					}
				})
			},
			// 复制
			copy(data){
				// #ifndef H5
				uni.setClipboardData({
				    data: data,
				    success: function () {
						// app跟小程序会自动弹出提示
				    }
				});
				// #endif
			}
		},
		computed:{
			formatCut() {
				if(this.order.orderStatus!=='NOPAY')return
				if (Number(this.cut_time) <= 0) {
					this.order.orderStatus='FAILED';
					return 0
				} else {
					var secondTime = parseInt(this.cut_time); // 秒
					var minuteTime = 0; // 分
					var hourTime = 0; // 小时
					if (secondTime > 60) { //如果秒数大于60，将秒数转换成整数
						//获取分钟，除以60取整数，得到整数分钟
						minuteTime = parseInt(secondTime / 60);
						//获取秒数，秒数取佘，得到整数秒数
						secondTime = parseInt(secondTime % 60);
						//如果分钟大于60，将分钟转换成小时
						if (minuteTime > 60) {
							//获取小时，获取分钟除以60，得到整数小时
							hourTime = parseInt(minuteTime / 60);
							//获取小时后取佘的分，获取分钟除以60取佘的分
							minuteTime = parseInt(minuteTime % 60);
						}
					}
					var result = "" + this.$common.zeroPadd(parseInt(secondTime), 2);
					result = "" + this.$common.zeroPadd(parseInt(minuteTime), 2) + "分" + result;
					result = "" + this.$common.zeroPadd(parseInt(hourTime), 2) + "小时" + result;
					return result;
				}
			}
		},
		onUnload() {
			clearInterval(cut1)
			cut1 = null;
		}
	}
</script>

